body {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  outline: none;
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  border: none;
}
button {
  border: none;
  padding: 0;
  cursor: pointer;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.container {
  max-width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}
@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media screen and (min-width: 1024px) {
  .container {
    width: 1024px;
    padding-left: 32px;
    padding-right: 32px;
  }
}
.current {
  border-bottom: 2px solid var(--font-orange-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  text-decoration: none;
  color: inherit;
}
